<template>
    <div>
     <headerNav title=""/>
      <van-address-edit
      :area-list="areaList"
      :showDelete="showDelete"
      show-set-default
      @save="onSave"
      @delete="onDelete"
      :addressInfo="info"
      />
    </div>
</template>

<script>
import areaList from '../../../data/area';
import { GetAddressById ,SaveAddress,DelAddress} from "@/api/address";

import { AddressEdit } from 'vant';
export default {
    components:{
        [AddressEdit.name]:AddressEdit,
    },
    data() {
    return {
      areaList,
      showDelete:false,
      info:{

      },
    }
  },

  methods: {
    onSave(data) {
      SaveAddress(data).then(response=>{
        this.$toast('');
        this.$router.go(-1);
      })
    },
    onDelete(data) {
      DelAddress(data.id).then(response=>{
        this.$toast('');
        this.$router.go(-1);
      })
    },
  },
  created:function(){
    var id=this.$route.query.id;
    if(id>0){
      this.showDelete=true;
      GetAddressById(id).then(response=>{
        let data = response.data.address
        this.info=data;
      })
    }
  }

}
</script>

<style>
.van-picker__toolbar{
  font-size: 16px;
}
</style>
